/* Container for the news page */
.news-page {
    background-color: #f8f9fa;
    padding: 20px 50px;
  }
  
  /* Title */
  .page-title {
    text-align: center;
    font-size: 36px;
    margin-bottom: 40px;
    color: #22262d;
    position: relative;
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
  }
  
  /* News List Container */
  .news-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .news-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
  
  /* News Card */
  .news-card {
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  
  .news-card:hover {
    transform: scale(1.03);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  }
  
  /* Image Styling */
  .news-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-bottom: 1px solid #ddd;
  }
  
  /* News Content */
  .news-content {
    padding: 20px;
  }
  
  .news-content h2 {
    font-size: 22px;
    color: #22262d;
    margin-bottom: 10px;
  }
  
  .news-content p {
    font-size: 16px;
    color: #555;
    margin-bottom: 20px;
  }
  
  /* Read More Button */
  .read-more-btn {
    background-color: #c0aa55;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    text-decoration: none;
    font-size: 14px;
    transition: background-color 0.3s ease;
  }
  
  .read-more-btn:hover {
    background-color: #444;
  }
  
  /* Animation for cards on scroll */
  .news-card {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.6s ease-in-out, transform 0.6s ease-in-out;
  }
  
  .news-card.visible {
    opacity: 1;
    transform: translateY(0);
  }
  
  /* Responsive Styles */
  @media (max-width: 1024px) {
    .news-list {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 768px) {
    .news-list {
      grid-template-columns: 1fr;
    }
  }
  